html,
body,
#root {
    margin: 0;
    padding: 0;
    height: 100%; /* Ensure full height for layout purposes */
    width: 100%; /* Ensure full width for layout purposes */

    /* Define system font stack with fallbacks for better cross-platform consistency */
    font-family:
        system-ui,
        -apple-system,
        BlinkMacSystemFont,
        'Segoe UI',
        Roboto,
        Oxygen-Sans,
        Ubuntu,
        Cantarell,
        'Helvetica Neue',
        Arial,
        sans-serif;

    font-size: 14px; /* Base font size for the application */
    background-color: #fff; /* Set default background color */
    color: #293239; /* Set default text color */
    overflow-x: hidden; /* Prevent horizontal scroll */
}

/* Utility class to remove all padding */
.no-padding {
    padding: 0 !important;
}
